.kv_area {
  width: 100%;
  height: 140px;
  background: url(/company/img/kv.jpg) center/cover;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 70px;
}
.kv_area h1 {
  width: 240px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 30px;
  background: rgba(0, 0, 0, 0.8);
  letter-spacing: 10px;
  padding-left: 10px;
}

.info_area {
  width: 100%;
  max-width: 1080px;
  margin: 0 auto;
}
.info_area .block h2 {
  font-size: 20px;
  color: #75a8ea;
  line-height: 1;
  padding: 10px 25px;
  border: 1px solid #75a8ea;
  display: inline-block;
  margin-bottom: 25px;
}
.info_area .block.col2 {
  display: flex;
  justify-content: space-between;
}
.info_area .block.col2 > * {
  width: 47%;
}
.info_area .block.col2 dl dt {
  width: 130px;
}
.info_area .block.col2 dl dd {
  width: calc(100% - 130px);
}
.info_area .block dl {
  display: flex;
  flex-wrap: wrap;
}
.info_area .block dl + h2 {
  margin-top: 45px;
}
.info_area .block dl dt {
  font-size: 16px;
  font-weight: bold;
  color: #999;
  padding-bottom: 10px;
  border-bottom: 1px solid #999;
}
.info_area .block dl dt:not(:last-of-type) {
  margin-bottom: 10px;
}
.info_area .block dl dd {
  font-size: 16px;
  padding-bottom: 10px;
  border-bottom: 1px solid #999;
}
.info_area .block dl dd:not(:last-child) {
  margin-bottom: 10px;
}
.info_area .block dl dd a {
  text-decoration: underline;
}
.info_area .block dl dd span {
  color: #f70;
}